<template>
    <div class="mechanism">
        <div class="mechTop">
            <div class="topTitle">
                <p>法律法规</p>
            </div>
        </div>
        <div class="mechContent">
            <el-container>
                <el-aside width="200px">
                    <div class="treeTitle"><span>全部</span></div>
                    <div class="treeData">
                        <el-tree
                            :data="data"
                            :props="defaultProps"
                            accordion
                            @node-click="handleNodeClick">
                        </el-tree>
                    </div>
                </el-aside>
                <el-container>
                    <el-header style="text-align: center;height: 40px">
                        <div class="yijiTitle">法律法规</div>
                    </el-header>
                    <el-main>
                        <div class="checkBox">
                            <el-checkbox-group v-model="checkedCities">
                                <el-checkbox v-for="(item, index) in cities" :label="item.id" :key="index">{{item.name}}</el-checkbox>
                            </el-checkbox-group>
                        </div>
                        <div class="richText">
                            <h2>第一章 总则</h2>
                            <span>发布时间:2019-12-23  来源：本站原创  作者：佚名 </span>
                            <div class="news_content">
                                <h2 style="text-align: left;text-indent: 2em;font-weight: 700;font-size: 18px;width: 112px;float:left">人民调解</h2><span>是指人民调解委员会通过说服、疏导等方法，促使当事人在平等协商基础上自愿达成</span>
                                <p>调解协议，解决纠纷的活动。</p>
                                <p>调解原则:</p>
                                <p>(一)在当事人自愿、平等的基础上进行调解;</p>
                                <p>(二)不违背法律、法规和国家政策;</p>
                                <p>(三)尊重当事人的权利，不得因调解而阻止当事人依法通过仲裁、行政、司法等途径维护自己的权利。</p>
                                <p>申请调解所需提供材料：</p>
                                <p>患方：</p>
                                <p>1、调解申请书</p>
                                <p>2、法定代表人身份证及证明书</p>
                                <p>3、委托代理人授权委托书</p>
                                <p>4、委托代理人身份证原件及复印件</p>
                                <p>5、申诉材料</p>
                                <p>6、纠纷相关证据材料。</p>
                                <p>医方：</p>
                                <p>1、调解申请书 </p>
                                <p>2、法定代表人身份证及证明书</p>
                                <p>3、委托代理人授权委托书 </p>
                                <p>4、委托代理人身份证原件及复印件 </p>
                                <p>5、执业许可证复印件 </p>
                                <p>6、当事人执业证及身份证复印件 </p>
                                <p>7、医疗纠纷发生经过及调查处理情况。</p>
                                <p>8、针对患方提出的问题和要求给予书面回复。</p>
                                <p>9、纠纷相关证据材料。</p>
                            </div>
                        </div>
                    </el-main>
                </el-container>
            </el-container>
        </div>
    </div>
</template>
<script>
export default {
    data() {
        return{
            data: [{
                label: '宪法',
                children: [] 
            },{
                label: '民法',
                children: []
            },{
                label: '刑法',
                children: []
            },{
                label: '行政法',
                children: []
            },{
                label: '民事诉讼法',
                children: []
            },{
                label: '刑事诉讼法',
                children: []
            },{
                label: '行政诉讼法',
                children: []
            },{
                label: '医疗事故处罚条例',
                children: []
            }],
            defaultProps: {
                children: 'children',
                label: 'label'
            },
            input4: '',
            checkedCities: [],
            cities: [
                {name: '第一章 总则', id: '1'},
                {name: '第一章 总则', id: '2'},
                {name: '第一章 总则', id: '3'},
                {name: '第一章 总则', id: '4'},
                {name: '第一章 总则', id: '5'},
                {name: '第一章 总则', id: '6'},
            ],
        }
    },
    methods: {
         handleNodeClick(data) {
            console.log(data);
        }
    }
}
</script>>
<style scoped>
.mechTop{
    width: 100%;
    height: 315px;
    background-image: url(../assets/mechanism.png);
    background-repeat:no-repeat;
    background-size: cover;
    position: relative;
}
.topTitle{
    height: 90px;
    background-color: #fff;
    margin: 0 10%;
    position: absolute;
    bottom: 0;
    left: 10%;
    right: 10%;
    border: 1px solid rgb(227, 227, 227);
}
.topTitle p{
    text-align: left;
    font-family: SourceHanSansSC;
    font-weight: 400;
    font-size: 20px;
    color: rgb(16, 16, 16);
    margin: 0 35px;
    line-height: 90px
}
.mechContent{
    padding: 30px 10%;
}
.treeTitle{
    border-left: 2px solid rgb(13, 114, 183);
    font-family: SourceHanSansSC;
    font-weight: 400;
    font-size: 14px;
    color: rgb(16, 16, 16);
    font-style: normal;
    letter-spacing: 0px;
    line-height: 20px;
    padding-left: 20px;
    text-align: left
}
.checkBox{
    text-align: left;
    margin-top: 15px;
    margin-bottom: 30px;
}
.yijiTitle{
    height: 40px;
    background-color: rgb(241, 241, 241);
    border-left: 3px solid rgb(13, 114, 183);
    text-align: left;
    padding-left: 15px;
    line-height: 40px;
    font-weight: 400;
    font-size: 16px;
    color: rgb(16, 16, 16);
}
.richText{
    padding-top: 20px;
    background-color: rgb(248, 248, 248);
}
.richText h2 {
    margin: 0
}
.serviceBox{
    padding: 0 20px
}
.news_content{
    border-top: 1px dashed;
    margin: 10px 40px;
    padding-top: 20px;
    text-align: left;
    font-weight: 400;
    font-size: 16px;
    color: rgb(16, 16, 16);
    text-indent: 2em;
}
</style>
<style>
.el-tree{
    margin-top: 15px;
    border-top: 1px solid #ccc
}
.el-tree-node__content{
    height: 45px !important;
    border-bottom: 1px solid #ccc
}
.el-tree-node:focus>.el-tree-node__content{
    background-color: rgb(13, 114, 183) !important;
    color: #fff
}
.el-tree-node__content:hover{
    background-color: rgb(13, 114, 183) !important;
    color: #fff
}
</style>


